import React from 'react';
import img1 from './images/Doraemon.png';


function About(){
    return(
        <div>
            <h1 align="center">Doraemon</h1>
            <img src={img1} width='300' height='300'></img>
            <h3>Doraemon's story revolves around an earless robotic cat named Doraemon, who travels back in time from the 22nd century to aid a boy named Nobita Nobi.</h3>
           
        </div>
    )
}
export default About;